import React from 'react';
import './Cards.css';
import CardItem from './CardItem';

function Cards() {
  return (
    <div className='cards'>
      <h1>Don't miss the wonders of nature!</h1>
      <div className='cards__container'>
        <div className='cards__wrapper'>
          <ul className='cards__items'>
            <CardItem
              src='Images\River_Swat_Pakistan_3.jpg'
              text='Explore the hidden rivers of Swat'
              label='Adventure'
              path='/products'
            />
            <CardItem
              src='Images\images (2).jpg'
              text='Travel through the mountains of Balochistan'
              label='Luxury'
              path='/services'
            />
          </ul>
          <ul className='cards__items'>
            <CardItem
              src='Images\swat-valley.jpg'
              text='Set sail in Swat waters'
              label='Mystery'
              path='/products'
            />
            <CardItem
              src='Images\green-forest-with-clouds-snow-capped-mountains-hunza-valley-pakistan_67721-310.jpg'
              text='Experience the Swat mountains'
              label='Adventure'
              path='/products'
            />
            <CardItem
              src='Images\download (4).jpg'
              text='Charming waterfalls of Balochistan'
              label='Adrenaline'
              path='/services'
            />
          </ul>
        </div>
      </div>
    </div>
  );
}

export default Cards;