import React from 'react'
import CardItem from './CardItem'
import './Balochistan.css'

function Balochistan() {
    return (
        <div className='cards'>
            <h1>
                Balochistan            
            </h1>
        <p>
        Balochistan is one of the four provinces of Pakistan. It is the largest province in terms of land area, forming the southwestern region of the country, but is the least populated. Its provincial capital and largest city is Quetta.
        </p>
        <div className='cards__container'>
          <div className='cards__wrapper'>
            <ul className='cards__items'>
            <CardItem
              
              src='Images\download (4).jpg'
              

              />
            
            
            <CardItem
              src='Images\ES6Sw6HXYAUX1yT.jpg'
              
            />
          </ul>
          <ul className='cards__items'>
            <CardItem
              src='Images\download (2).jpg'
              
            />
            <CardItem
              src='Images\download (3).jpg'
            
            />
            <CardItem
              src='Images\download.jpg'
              
            />
          </ul>
        </div>
      </div>
    </div>
  );
}
export default Balochistan;
              
            