import React from 'react'
import CardItem from './CardItem'
import './SwatCards.css'

function SwatCards() {
    return (
        <div className='cards'>
            <h1>
                Swat
            </h1>
        <p>
        Swat District is a district in Malakand Division of Khyber Pakhtunkhwa province in Pakistan. Centred upon the upper portions of the Swat River, Swat was a major centre of early Buddhist thought as part of the Gandhara kingdom, and today is littered with ruins from that era.
        </p>
        <div className='cards__container'>
          <div className='cards__wrapper'>
            <ul className='cards__items'>
            <CardItem
              
              src='Images\Slider-1-8-1024x373.jpg'
              

              />
            
            
            <CardItem
              src='Images\River_Swat_Pakistan_3.jpg'
              
            />
          </ul>
          <ul className='cards__items'>
            <CardItem
              src='Images\Gabral River. Swat.jpg'
              
            />
            <CardItem
              src='Images\swat-is-also-known-to.jpg'
            
            />
            <CardItem
              src='Images\swat-valley.jpg'
              
            />
          </ul>
        </div>
      </div>
    </div>
  );
}
              
            
    export default SwatCards;
